import { createSelector, createSlice, PayloadAction } from '@reduxjs/toolkit';
import { Pokemon } from '../models/pokemon.model';

export const POKEMONS_STORE_KEY = 'pokemonsStore';

/**
 * Store state
 */
export interface IPokemonsStoreState {
  pokemons: Pokemon[];
}

/**
 * Initial store state
 */
export const initialPokemonsStoreState: IPokemonsStoreState = {
  pokemons: [],
};

export const pokemonsStoreSlice = createSlice({
  name: POKEMONS_STORE_KEY,
  initialState: initialPokemonsStoreState,
  reducers: {
    setPokemons: (state: IPokemonsStoreState, action: PayloadAction<Pokemon[]>) => {
      if (action?.payload) {
        state.pokemons = action.payload;
      }
    },
  },
});

/*
 * Export action creators to be dispatched. For use with the `useDispatch` hook.
 *
 * See: https://react-redux.js.org/next/api/hooks#usedispatch
 */
export const { setPokemons } = pokemonsStoreSlice.actions;

/*
 * Export selectors to query state. For use with the `useSelector` hook.
 *
 * e.g.
 * ```
 * const count = useSelector(selectGatewayId);
 * ```
 *
 * See: https://react-redux.js.org/next/api/hooks#useselector
 */
export const getPokemonsStoreState = (rootState: any): IPokemonsStoreState => rootState[POKEMONS_STORE_KEY];

export const selectPokemons = createSelector(getPokemonsStoreState, (state): Pokemon[] => state.pokemons);

/*
 * Export reducer for store configuration.
 */
export const pokemonsStoreReducer = pokemonsStoreSlice.reducer;

