import { combineReducers, configureStore, PayloadAction } from '@reduxjs/toolkit';
import {
  initialPokemonTypesStoreState,
  POKEMON_TYPES_STORE_KEY,
  pokemonTypesStoreReducer,
} from './pokemon-types-store.slice';
import { initialSearchTermStoreState, SEARCH_TERM_STORE_KEY, searchTermStoreReducer } from './search-term-store.slice';
import { initialSelectedPokemonStoreState, SELECTED_POKEMON_STORE_KEY, selectedPokemonStoreReducer } from './selected-pokemon-store.slice';
import { initialPokemonsStoreState, POKEMONS_STORE_KEY, pokemonsStoreReducer } from './pokemons-store.slice';

// Initial Store State
const initialState = {
  [SELECTED_POKEMON_STORE_KEY]: initialSelectedPokemonStoreState,
  [POKEMONS_STORE_KEY]: initialPokemonsStoreState,
  [POKEMON_TYPES_STORE_KEY]: initialPokemonTypesStoreState,
  [SEARCH_TERM_STORE_KEY]: initialSearchTermStoreState,
};

// Whenever an action is dispatched, Redux will update each top-level application state property using
// the reducer with the matching name. It's important that the names match exactly, and that the reducer
// acts on the corresponding ApplicationState property type.
const reducers = {
  [SELECTED_POKEMON_STORE_KEY]: selectedPokemonStoreReducer,
  [POKEMONS_STORE_KEY]: pokemonsStoreReducer,
  [POKEMON_TYPES_STORE_KEY]: pokemonTypesStoreReducer,
  [SEARCH_TERM_STORE_KEY]: searchTermStoreReducer,
};

/**
 * This will combine the dynamic portions of initial state with persisted state
 * @returns
 */
function getInitialStoreState() {
  return { ...initialState };
}
// Combined reducer of all app reducers
const appReducer = combineReducers(reducers);
const rootReducer = (state: any, action: PayloadAction<any>) => {
  return appReducer(state, action);
};

// configure store
export const ReduxStore = configureStore({
  preloadedState: getInitialStoreState(),
  reducer: rootReducer,
});

export default ReduxStore;
