import { Pokemon } from '../models/pokemon.model';
import { PokemonType } from '../models/type.model';

export const retrievePokemonList = async (): Promise<Pokemon[]> => {
  const response = await fetch('https://tyradex.tech/api/v1/pokemon');
  if (!response.ok) {
    console.error(`Error fetching Pokemons: ${response.statusText}`);
    return;
  }
  const list =  await response.json();
  return list.filter((pokemon: Pokemon) => pokemon.pokedex_id !== 0);
};

export const retrieveTypeList = async (): Promise<PokemonType[]> => {
  const response = await fetch('https://tyradex.tech/api/v1/types');
  if (!response.ok) {
    console.error(`Error fetching Pokemon types: ${response.statusText}`);
    return;
  }
  return await response.json();
};
