import { IonModal, setupIonicReact } from '@ionic/react';
import '@ionic/react/css/core.css';

import './pokemon-detail.css';
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { clearSelectedPokemon, selectSelectedPokemon } from '../../stores/selected-pokemon-store.slice';
import { selectPokemons } from '../../stores/pokemons-store.slice';
import { Pokemon } from '../../models/pokemon.model';

setupIonicReact();

const PokemonDetail = () => {
  const [isOpen, setIsOpen] = useState(false);
  const [useShinySprite, setUseShinySprite] = useState(false);
  const selectedPokemon = useSelector(selectSelectedPokemon);
  const pokemonList = useSelector(selectPokemons);
  const dispatch = useDispatch();

  useEffect(() => {
    if (selectedPokemon) {
      setIsOpen(true);
    } else {
      setIsOpen(false);
    }
    return () => {
      setIsOpen(false);
    };
  }, [selectedPokemon]);

  const findPreEvolutionSprite = (currentPokemon: Pokemon): string => {
    return pokemonList.find((pokemon: Pokemon) => pokemon.pokedex_id === currentPokemon.evolution.pre.at(-1).pokedex_id).sprites.regular;
  };

  const switchBetweenRegularAndShiny = () => {
    setUseShinySprite(!useShinySprite);
  };

  return (
    <IonModal className="pokemon-details" isOpen={isOpen} onDidDismiss={() => dispatch(clearSelectedPokemon())}>
      {selectedPokemon && (
        <div className={'nameimageinfo ' + selectedPokemon.types[0].name.toLowerCase()}>
          <table className="nameheaders">
            <tbody>
              <tr>
                <td className="basic" colSpan={3}>
                  {!selectedPokemon?.evolution?.pre && <span>Pokmon de base</span>}

                  {selectedPokemon?.evolution?.pre && (
                    <>
                      <span className="basic">
                        <img className="basic" src={findPreEvolutionSprite(selectedPokemon)} alt={selectedPokemon.name.fr} />
                      </span>
                      <span>{'  Evolution de ' + selectedPokemon.evolution.pre.at(-1).name}</span>
                    </>
                  )}
                </td>
              </tr>
            </tbody>
            <tbody>
              <tr>
                <td className="nameofanimal">{selectedPokemon.name.fr}</td>
                <td className="hp">{selectedPokemon.stats.hp + ' PV'}</td>
                <td className="typesign">
                  <img alt={selectedPokemon.types[0].name} className="typesign" src={selectedPokemon.types[0].image}></img>{' '}
                  {selectedPokemon.types[1] && (
                    <img alt={selectedPokemon.types[1].name} className="typesign" src={selectedPokemon.types[1].image}></img>
                  )}
                </td>
              </tr>
            </tbody>
          </table>
          <div className="headshot">
            <img
              className="onTop"
              src={useShinySprite ? selectedPokemon.sprites.regular : selectedPokemon.sprites.shiny}
              alt={selectedPokemon.name.fr}
              onClick={() => switchBetweenRegularAndShiny()}
            />
            <img
              className="headshot"
              src={useShinySprite ? selectedPokemon.sprites.shiny : selectedPokemon.sprites.regular}
              alt={selectedPokemon.name.fr}
            />
          </div>
          {/*<img className="headshot" src="https://heatherketten.files.wordpress.com/2018/03/pikachu.png" alt="Pikachu"></img>*/}
          <p className="description">{selectedPokemon.category + '. Taille ' + selectedPokemon.height + ', Poids ' + selectedPokemon.weight + '.'}</p>
          <div className="infoonly">
            <table className="stats">
              <tbody>
                <tr>
                  <td className="energy">
                    <img alt="TO_DO" src="https://heatherketten.files.wordpress.com/2018/03/nrg_normal.png" width="20" height="20"></img>
                  </td>
                  <td>
                    <span className="label">Growl</span> <span className="labeltext"> </span>
                  </td>
                  <td className="damage">10</td>
                </tr>
              </tbody>
            </table>
            <hr></hr>
            <table className="stats">
              <tbody>
                <tr>
                  <td className="energy">
                    <img alt="TO_DO" src="https://heatherketten.files.wordpress.com/2018/03/nrg_electric.png" width="25" height="25"></img>
                    <img alt="TO_DO" src="https://heatherketten.files.wordpress.com/2018/03/nrg_electric.png" width="25" height="25"></img>
                  </td>
                  <td>
                    <span className="label">Thundershock</span>{' '}
                    <span className="labeltext">Flip a coin. If tails, Pikachu does 10 damage to itself.</span>
                  </td>
                  <td className="damage">30</td>
                </tr>
              </tbody>
            </table>
            <hr></hr>
            <table className="costs">
              <tbody>
                <tr className="costheaders">
                  <td>Faiblesses</td>
                  <td>Rsistances</td>
                </tr>
              </tbody>
              <tbody>
                <tr className="costicons">
                  <td>
                    {selectedPokemon?.resistances.map((resistance: { name: string; multiplier: number }) =>
                      resistance.multiplier > 1 ? (
                        <span className="costicons">
                          <img
                            alt={resistance.name}
                            className="costicons"
                            src={
                              'https://raw.githubusercontent.com/Yarkis01/TyraDex/images/types/' +
                              resistance.name
                                .toLowerCase()
                                .normalize('NFD')
                                .replace(/[\u0300-\u036f]/g, '') +
                              '.png'
                            }
                          ></img>
                        </span>
                      ) : null
                    )}
                  </td>
                  <td>
                    {selectedPokemon?.resistances.map((resistance: { name: string; multiplier: number }) =>
                      resistance.multiplier < 1 ? (
                        <span className="costicons">
                          <img
                            alt={resistance.name}
                            className="costicons"
                            src={
                              'https://raw.githubusercontent.com/Yarkis01/TyraDex/images/types/' +
                              resistance.name
                                .toLowerCase()
                                .normalize('NFD')
                                .replace(/[\u0300-\u036f]/g, '') +
                              '.png'
                            }
                          ></img>
                        </span>
                      ) : null
                    )}
                  </td>
                </tr>
              </tbody>
            </table>
            <p className="italicize">When several of these Pokemon gather, their electricity could build and cause lightning storms.</p>
            <p className="bottomtext">
              <strong>Illus. Mitsuhiro Arita</strong> C 1995, 96, 98 Nintendo Creatures, GAMEFREAK C 1999 Wizards. <strong>58/102 o</strong>{' '}
            </p>
          </div>
        </div>
      )}
    </IonModal>
  );
};

export default PokemonDetail;
