import { createSelector, createSlice, PayloadAction } from '@reduxjs/toolkit';
import { Pokemon } from '../models/pokemon.model';

export const SELECTED_POKEMON_STORE_KEY = 'selectedPokemonStore';

/**
 * Store state
 */
export interface ISelectedPokemonStoreState {
  selectedPokemon: Pokemon;
}

/**
 * Initial store state
 */
export const initialSelectedPokemonStoreState: ISelectedPokemonStoreState = {
  selectedPokemon: null,
};

export const selectedPokemonStoreSlice = createSlice({
  name: SELECTED_POKEMON_STORE_KEY,
  initialState: initialSelectedPokemonStoreState,
  reducers: {
    setSelectedPokemon: (state: ISelectedPokemonStoreState, action: PayloadAction<Pokemon>) => {
      if (action?.payload) {
        state.selectedPokemon = action.payload;
      }
    },
    clearSelectedPokemon: (state: ISelectedPokemonStoreState, action: PayloadAction<Pokemon>) => {
      state.selectedPokemon = undefined;
    },
  },
});

/*
 * Export action creators to be dispatched. For use with the `useDispatch` hook.
 *
 * See: https://react-redux.js.org/next/api/hooks#usedispatch
 */
export const { setSelectedPokemon, clearSelectedPokemon } = selectedPokemonStoreSlice.actions;

/*
 * Export selectors to query state. For use with the `useSelector` hook.
 *
 * e.g.
 * ```
 * const count = useSelector(selectGatewayId);
 * ```
 *
 * See: https://react-redux.js.org/next/api/hooks#useselector
 */
export const getSelectedPokemonStoreState = (rootState: any): ISelectedPokemonStoreState => rootState[SELECTED_POKEMON_STORE_KEY];

export const selectSelectedPokemon = createSelector(getSelectedPokemonStoreState, (state): Pokemon => state.selectedPokemon);

/*
 * Export reducer for store configuration.
 */
export const selectedPokemonStoreReducer = selectedPokemonStoreSlice.reducer;
