import { createSelector, createSlice, PayloadAction } from '@reduxjs/toolkit';

export const SEARCH_TERM_STORE_KEY = 'searchTermStore';

/**
 * Store state
 */
export interface ISearchTermStoreState {
  searchTerm: string;
}

/**
 * Initial store state
 */
export const initialSearchTermStoreState: ISearchTermStoreState = {
  searchTerm: '',
};

export const searchTermStoreSlice = createSlice({
  name: SEARCH_TERM_STORE_KEY,
  initialState: initialSearchTermStoreState,
  reducers: {
    setSearchTerm: (state: ISearchTermStoreState, action: PayloadAction<string>) => {
      if (action?.payload) {
        state.searchTerm = action.payload;
      }
    },
  },
});

/*
 * Export action creators to be dispatched. For use with the `useDispatch` hook.
 *
 * See: https://react-redux.js.org/next/api/hooks#usedispatch
 */
export const { setSearchTerm } = searchTermStoreSlice.actions;

/*
 * Export selectors to query state. For use with the `useSelector` hook.
 *
 * e.g.
 * ```
 * const count = useSelector(selectGatewayId);
 * ```
 *
 * See: https://react-redux.js.org/next/api/hooks#useselector
 */
export const getSearchTermStoreState = (rootState: any): ISearchTermStoreState => rootState[SEARCH_TERM_STORE_KEY];

export const selectSearchTerm = createSelector(getSearchTermStoreState, (state): string => state.searchTerm);

/*
 * Export reducer for store configuration.
 */
export const searchTermStoreReducer = searchTermStoreSlice.reducer;
