import { createSelector, createSlice, PayloadAction } from '@reduxjs/toolkit';
import { PokemonType } from '../models/type.model';

export const POKEMON_TYPES_STORE_KEY = 'pokemonTypesStore';

/**
 * Store state
 */
export interface IPokemonTypesStoreState {
  pokemonTypes: PokemonType[];
}

/**
 * Initial store state
 */
export const initialPokemonTypesStoreState: IPokemonTypesStoreState = {
  pokemonTypes: [],
};

export const pokemonTypesStoreSlice = createSlice({
  name: POKEMON_TYPES_STORE_KEY,
  initialState: initialPokemonTypesStoreState,
  reducers: {
    setPokemonTypes: (state: IPokemonTypesStoreState, action: PayloadAction<PokemonType[]>) => {
      if (action?.payload) {
        state.pokemonTypes = action.payload;
      }
    },
    setToggleSelectedType: (state: IPokemonTypesStoreState, action: PayloadAction<{ typeId: number; selected: boolean }>) => {
      if (action?.payload) {
        state.pokemonTypes.find((type: PokemonType) => type.id === action.payload.typeId).selected = action.payload.selected;
      }
    },
  },
});

/*
 * Export action creators to be dispatched. For use with the `useDispatch` hook.
 *
 * See: https://react-redux.js.org/next/api/hooks#usedispatch
 */
export const { setPokemonTypes, setToggleSelectedType } = pokemonTypesStoreSlice.actions;

/*
 * Export selectors to query state. For use with the `useSelector` hook.
 *
 * e.g.
 * ```
 * const count = useSelector(selectGatewayId);
 * ```
 *
 * See: https://react-redux.js.org/next/api/hooks#useselector
 */
export const getPokemonTypesStoreState = (rootState: any): IPokemonTypesStoreState => rootState[POKEMON_TYPES_STORE_KEY];

export const selectPokemonTypes = createSelector(getPokemonTypesStoreState, (state): PokemonType[] => state.pokemonTypes);

/*
 * Export reducer for store configuration.
 */
export const pokemonTypesStoreReducer = pokemonTypesStoreSlice.reducer;
