import { IonButton, IonCol, IonGrid, IonRow, IonSearchbar, setupIonicReact } from '@ionic/react';
import '@ionic/react/css/core.css';

import './header.css';
import React, { useEffect, useLayoutEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import logo from '../../data/logo.png';
import { PokemonType } from '../../models/type.model';
import { retrieveTypeList } from '../../services/pokemon-rest.service';
import { selectPokemonTypes, setPokemonTypes, setToggleSelectedType } from '../../stores/pokemon-types-store.slice';
import { setSearchTerm } from '../../stores/search-term-store.slice';

setupIonicReact();

const Header = () => {
  const pokemonTypesList = useSelector(selectPokemonTypes);
  const dispatch = useDispatch();

  useEffect(() => {
    retrieveTypeList().then((pokemonTypes: PokemonType[]) => {
      pokemonTypes.forEach((pokemonType: PokemonType) => (pokemonType.selected = true));
      dispatch(setPokemonTypes(pokemonTypes));
    });
    return () => {
      // Do this on unmount
    };
  }, [dispatch]);

  useLayoutEffect(() => {
    // after dom render, to give focus on the searchbar for example
  }, []);

  const handleInput = (ev: Event) => {
    let query = '';
    const target = ev.target as HTMLIonSearchbarElement;
    if (target) query = target.value!.toLowerCase();

    dispatch(setSearchTerm(query));
  };

  const toggleSelectedType = (type: PokemonType) => {
    if (type.selected) {
      dispatch(setToggleSelectedType({ typeId: type.id, selected: false }));
    } else {
      dispatch(setToggleSelectedType({ typeId: type.id, selected: true }));
    }
  };

  const isSelected = (type: PokemonType): boolean => {
    return type.selected;
  };

  return (
    <>
      <img src={logo} className="logo" alt="Pokemon logo" />
      <IonGrid className="header-grid">
        <IonRow>
          <IonCol size="4" offset="1">
            <div className="search-container">
              <IonSearchbar type="text" class="search-bar" placeholder="Search..." onIonInput={(ev) => handleInput(ev)}></IonSearchbar>
            </div>
          </IonCol>
        </IonRow>
        <IonRow>
          <IonGrid>
            <IonRow>
              {pokemonTypesList.map((type: PokemonType) => (
                <IonCol key={'type-item' + type.id} className="type-item">
                  <IonButton color="light" fill="outline" size="small" onClick={() => toggleSelectedType(type)}>
                    <img className={isSelected(type) ? '' : 'fake-disabled'} slot="icon-only" src={type.sprites} alt={type.name.fr} />
                  </IonButton>
                </IonCol>
              ))}
            </IonRow>
          </IonGrid>
        </IonRow>
      </IonGrid>
    </>
  );
};

export default Header;
