import { IonCol, IonGrid, IonRow, setupIonicReact } from '@ionic/react';
import React, { useEffect, useMemo, useState } from 'react';
import '@ionic/react/css/core.css';

import './pokemon-list.css';
import { useDispatch, useSelector } from 'react-redux';
import { LightPokemonType, Pokemon } from '../../models/pokemon.model';
import { PokemonType } from '../../models/type.model';
import { retrievePokemonList } from '../../services/pokemon-rest.service';
import { selectPokemonTypes } from '../../stores/pokemon-types-store.slice';
import { selectPokemons, setPokemons } from '../../stores/pokemons-store.slice';
import { selectSearchTerm } from '../../stores/search-term-store.slice';
import { setSelectedPokemon } from '../../stores/selected-pokemon-store.slice';
import PokemonItem from '../pokemon-item/pokemon-item';

setupIonicReact();

const PokemonList = () => {
  const [isLoadingList, setIsLoadingList] = useState<boolean>(true);
  const pokemonList = useSelector(selectPokemons);
  const searchTerm = useSelector(selectSearchTerm);
  const typeList = useSelector(selectPokemonTypes);
  const dispatch = useDispatch();

  useEffect(() => {
    retrievePokemonList().then((pokemonList: Pokemon[]) => dispatch(setPokemons(pokemonList)));
  }, [dispatch]);

  useEffect(() => {
    if (pokemonList.length) {
      setIsLoadingList(false);
    }
    return () => {};
  }, [pokemonList]);

  const filteredPokemonList = useMemo(() => {
    return pokemonList
      .filter((pokemon: Pokemon) => pokemon.name.fr.toLowerCase().includes(searchTerm.toLowerCase()))
      .filter((pokemon: Pokemon) =>
        pokemon.types.some((lightType: LightPokemonType) => typeList.find((type: PokemonType) => type.name.fr === lightType.name).selected)
      );
  }, [searchTerm, pokemonList, typeList]);

  const showPokemon = (id: number) => {
    const pokemon = pokemonList.find((pokemon: Pokemon) => pokemon.pokedex_id === id);
    dispatch(setSelectedPokemon(pokemon));
  };

  return (
    <>
      {isLoadingList && <div>ALO LOADING</div>}
      <IonGrid>
        <IonRow>
          {filteredPokemonList.map((pokemon: Pokemon) => (
            <IonCol key={'pokemon-item' + pokemon.pokedex_id} size="6" size-md="4" size-lg="2" className="pokemon-item">
              <PokemonItem pokemon={pokemon} showPokemon={showPokemon}></PokemonItem>
            </IonCol>
          ))}
        </IonRow>
      </IonGrid>
    </>
  );
};

export default PokemonList;
